import React, {createRef} from "react"
import {TransitionGroup, CSSTransition} from "react-transition-group"
import classNames from "classnames";
import { v4 as uuid } from "uuid"

export default class Notifier extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            messages: []
        }
    }

    componentDidMount() {
        window.notify = this.notify
    }
    componentWillUnmount() {
        window.notify = null
    }

    notify = (msg) => {
        msg = { id: uuid(), type: msg.type, title: msg.title, text: msg.text }
        this.setState({ messages: [...this.state.messages.slice(-2), msg] })

        setTimeout(() => this.removeNotification(msg), 5000)
    }
    removeNotification = (msg) => {
        let messages = this.state.messages.slice()
        let changed = false
        for (let i in messages) {
            let message = messages[i]
            if (message.id === msg.id) {
                messages.splice(+i, 1)
                changed = true
                break;
            }
        }

        if (changed)
            this.setState({ messages: messages })
    }

    render() {
        let _notifications = this.state.messages.map((item, index) => {
            const nodeRef = createRef(null)
            return (
                <CSSTransition nodeRef={nodeRef} key={item.id} timeout={300} classNames="notification" className={classNames({ "notification": true, ["type-" + item.type]: true })}>
                    <div ref={nodeRef}>
                        <div className="message">
                            <span>{item.title}</span>
                            <span>{item.text}</span>
                        </div>
                        <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg" className="icon">
                            <mask id="notification_icon_mask">
                                <path d="M20 10C20 15.5228 15.5228 20 10 20C4.47715 20 0 15.5228 0 10C0 4.47715 4.47715 0 10 0C15.5228 0 20 4.47715 20 10ZM2.23172 10C2.23172 14.2903 5.7097 17.7683 10 17.7683C14.2903 17.7683 17.7683 14.2903 17.7683 10C17.7683 5.7097 14.2903 2.23172 10 2.23172C5.7097 2.23172 2.23172 5.7097 2.23172 10Z" fill="white"/>
                            </mask>
                            <path d="M20 10C20 15.5228 15.5228 20 10 20C4.47715 20 0 15.5228 0 10C0 4.47715 4.47715 0 10 0C15.5228 0 20 4.47715 20 10ZM2.23172 10C2.23172 14.2903 5.7097 17.7683 10 17.7683C14.2903 17.7683 17.7683 14.2903 17.7683 10C17.7683 5.7097 14.2903 2.23172 10 2.23172C5.7097 2.23172 2.23172 5.7097 2.23172 10Z" fill="white" fillOpacity="0.25"/>
                            <circle cx="10" cy="10" r="10" stroke="#FFCB44" strokeWidth="8px" mask="url(#notification_icon_mask)"
                                strokeDasharray="1000 1000"
                                strokeDashoffset="1000"
                            />
                        </svg>
                    </div>
                </CSSTransition>
            )
        })
        return (
            <div className="Notifier">
                <TransitionGroup
                    timeout={300}
                    className="notifications"
                >
                    {_notifications}
                </TransitionGroup>
            </div>
        )
    }
}