import React, {createRef} from "react"
import {CSSTransition, TransitionGroup} from "react-transition-group"
import { v4 as uuid } from "uuid"
import icons from "./icons";

export default class Announcer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            messages: []
        }
    }

    componentDidMount() {
        window.announce = this.announce
    }
    componentWillUnmount() {
        window.announce = null
    }

    announce = (msg) => {
        msg = { id: uuid(), title: msg.title, text: msg.text, time: msg.time }
        this.setState({ messages: [...this.state.messages.slice(-2), msg] })

        setTimeout(() => this.removeAnnouncement(msg), msg.time || 5000)
    }
    removeAnnouncement = (msg) => {
        let messages = this.state.messages.slice()
        let changed = false
        for (let i in messages) {
            let message = messages[i]
            if (message.id === msg.id) {
                messages.splice(+i, 1)
                changed = true
                break;
            }
        }

        if (changed)
            this.setState({ messages: messages })
    }

    render() {
        let _announcements = this.state.messages.map((item) => {
            let nodeRef = createRef()
            return (
                <CSSTransition nodeRef={nodeRef} key={item.id} timeout={300} classNames="announcement" className="announcement">
                    <div ref={nodeRef}>
                        {icons.announcer}
                        <div className="wrap">
                            <span>{item.title}</span>
                            <span dangerouslySetInnerHTML={{ __html: item.text }} />
                        </div>
                    </div>
                </CSSTransition>
            )
        })
        return (
            <div className="Announcer">
                <TransitionGroup
                    timeout={300}
                    className="announcements"
                >
                    {_announcements}
                </TransitionGroup>
            </div>
        )
    }
}