export function SpeedometerCursor(props) {
    let _baseRotate = -164
    let _rotate = _baseRotate + (272 / props.maxSpeed * props.speed)
    return (
        <svg width="225" height="225" viewBox="0 0 225 225" fill="none" xmlns="http://www.w3.org/2000/svg" className="cursor" style={{ transform: `rotate(${_rotate}deg)`}}>
            <rect x="168.008" y="13" width="4.02159" height="13.6306" rx="1" transform="rotate(30 168.008 13)" fill="white"/>
        </svg>
    )
}

