import React, { useState } from "react"
import "./App.scss"
import icons, {MinimapAdditionalCircle, SpeedometerSpeed, SpeedometerCursor, SpeedometerFuel} from "./icons"
import Announcer from "./Announcer"
import Notifier from "./Notifier"
import { useNuiEvent } from 'fivem-nui-react-lib';
import $ from 'jquery';


function Minimap(props) {
    let _data = props.data
    return (
        <div className="Minimap">
            <Notifier />
            <div className="money">
                <div className="item cash">
                    <div className="icon">{icons.minimapMoneyCash}</div>
                    <span><b>$</b> {_data.money.cash}</span>
                </div>
                <div className="item bank">
                    <div className="icon">{icons.minimapMoneyBank}</div>
                    <span><b>$</b> {_data.money.bank}</span>
                </div>
                <div className="item another">
                    <div className="icon">{icons.minimapMoneyAnother}</div>
                    <span><b>$</b> {_data.money.another}</span>
                </div>
            </div>
            <div className="additional">
                <div className="item location">
                    {icons.minimapAdditionalLocation}
                    <div className="wrap">
                        <span>{_data.location.region}</span>
                        <span>{_data.location.street}</span>
                    </div>
                </div>
                <div className="item thirst">
                    <MinimapAdditionalCircle current={_data.thirst} />
                    <div className="wrap">
                        <span>DURST</span>
                        <span>BEDARF</span>
                    </div>
                </div>
                <div className="item food">
                    <MinimapAdditionalCircle current={_data.food} />
                    <div className="wrap">
                        <span>HUNGER</span>
                        <span>BEDARF</span>
                    </div>
                </div>
            </div>
        </div>
    )
}

function Speedometer(props) {
    let _data = props.data
    function replaceSpeed(x) {
        if (x === 0)
            return `000`

        x = `${x}`
        if (x.length === 1)
            return `00<b>${x}</b>`
        else if (x.length === 2)
            return `0<b>${x}</b>`
        else
            return `<b>${x}</b>`
    }
    return (
        <>
        {_data.show && 
        <div className="Speedometer">
            <SpeedometerSpeed speed={_data.speed} maxSpeed={_data.maxSpeed} />
            <SpeedometerCursor speed={_data.speed} maxSpeed={_data.maxSpeed} />
            <SpeedometerFuel fuel={_data.fuel} />
            <div className="wrap">
                <span>KM/H</span>
                <span dangerouslySetInnerHTML={{ __html: replaceSpeed(_data.speed) }} />
            </div>
        </div> }

        </>
    )
}


export default class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            minimap: {
                money: {
                    cash: "Laden...",
                    bank: "Laden...",
                    another: "Laden..."
                },
                location: {
                    region: "Laden...",
                    street: "Laden..."
                },
                thirst: 0,
                food: 0
            },
            speedometer: {
                show: false,
                maxSpeed: 0,
                speed: 0,
                fuel: 0
            }
        }
    }

    componentDidMount() {
        window.updateMoneyAccount = this.updateMoneyAccount;
        window.updateLocation = this.updateLocation;
        window.updateNeeds = this.updateNeeds;
        window.updateSpeedo = this.updateSpeedo;
        window.updateShowSpeedo = this.updateShowSpeedo;
        window.updateMaxSpeed = this.updateMaxSpeed;
        setTimeout(() => {
            this.trigger();
        }, 1000)
    }

    trigger() {
        setTimeout(() => {
            if (GetParentResourceName != undefined) { // eslint-disable-line
                $.post(`https://${GetParentResourceName()}/load`) // eslint-disable-line
            } else {
                this.trigger();
            }
        }, 1000)
    }

    componentWillUnmount() {
        window.updateMapValue = null;
    }

    updateMoneyAccount = (data) => {
        let minimap = this.state.minimap
        minimap.money[data.key] = data.value
        this.setState({minimap});
    }

    updateLocation = (data) => {
        let minimap = this.state.minimap
        minimap.location.region = data.region;
        minimap.location.street = data.street;
        this.setState({minimap});
    }

    updateNeeds = (data) => {
        let minimap = this.state.minimap
        minimap.thirst = data.thirst;
        minimap.food = data.food;
        this.setState({minimap});
    }

    updateSpeedo = (data) => {
        let speedometer = this.state.speedometer;
        speedometer.speed = data.speed;
        speedometer.fuel = data.fuel;
        this.setState({speedometer});
    }

    updateShowSpeedo = (data) => {
        let speedometer = this.state.speedometer;
        speedometer.show = data;
        this.setState({speedometer});
    }

    updateMaxSpeed = (data) => {
        let speedometer = this.state.speedometer;
        speedometer.maxSpeed = data;
        this.setState({speedometer});
    }

    render() {
        let _minimap = this.state.minimap
        let _speedometer = this.state.speedometer
        return (
            <div className="App">
                <Announcer />
                <Minimap data={_minimap} />
                <Speedometer data={_speedometer} />
            </div>
        )
    }
}

window.addEventListener("message", function(event)  {
    event = event.data;
    if (event.action == "updateMoneyAccount") {
        window.updateMoneyAccount(event.data);
    } else if (event.action == "updateLocation") {
        window.updateLocation(event.data)
    } else if (event.action == "updateNeeds") {
        window.updateNeeds(event.data)
    } else if (event.action == "updateSpeedo") {
        window.updateSpeedo(event.data)
    } else if (event.action == "updateShowSpeedo") {
        window.updateShowSpeedo(event.data);
    } else if (event.action == "updateMaxSpeed") {
        window.updateMaxSpeed(event.data);
    } else if (event.action == "notify") {
        window.notify(event.data);
    } else if (event.action == "announce") {
        window.announce(event.data);
    }
})
